#output_container {

  position: absolute;
  bottom: 0;
  left: 0;
  width: 360px;
  height: 240px;
 pointer-events: auto;

  -moz-transform: scaleX(-1);
  -webkit-transform: scaleX(-1);
  -o-transform: scaleX(-1);
  transform: scaleX(-1);

  video{
   position: absolute;
  }


  #output_canvas {
   width: 100%;
   height: 100%;
   position: absolute;


  }
}
